<template>
  <div class="header">
    <div class="header__container">
      <div class="header__logo"></div>
      <div class="header__navigation">
        <router-link v-for="item in navMenu" :to="item.href" :key="item.id">
          <div class="header__navigation-item" :class="{'header__navigation-item_active': activeItem(item.href)}">{{item.name}}</div>
        </router-link>
        <div class="header__navigation-item align-right" @click="logout"></div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'HeaderItem',
  data() {
    return {
      navMenu: [
        { name: '', href: `/search/${this.getLogin()}` },
        { name: '', href: `/favorites/${this.getLogin()}` },
      ],
    };
  },
  methods: {
    activeItem(href) {
      return this.$route.path === href;
    },
    logout() {
      localStorage.removeItem('token');
      this.$router.push('/');
    },
  },
};
</script>

<style lang="scss" src="styles/components/vHeader/vHeader.scss"/>
