<template>
  <div class="modal" v-if="modalType !== ''">
    <div class="modal__content">
      <div class="modal__title">{{`${modalAppearance[modalType]} `}}</div>
      <div class="modal__inputs">
        <div class="modal__row modal__row_text">
          <div class="modal__label label"></div>
          <input class="modal__input input" placeholder="  " v-model="request.body.text">
        </div>
        <div class="modal__row modal__row_text">
          <div class="modal__label"><span class="red">* </span></div>
          <input class="modal__input input" placeholder=" " v-model="request.name">
        </div>

        <div class="modal__row">
          <div class="modal__label"> </div>
          <v-select :list="orderTemplate" :placeholder="placeholder" :default-item="request.body.order" @value="receiveSelect"/>
        </div>
        <div class="modal__row modal__row_range">
          <div class="modal__label"> </div>
          <div class="modal__range">
            <v-range class="modal__range-input" :min="1" :max="50" :default="12" @value="receiveRange"/>
            <div class="modal__range-value input">{{request.body.maxResults}}</div>
          </div>
        </div>
      </div>
      <div class="modal__buttons">
        <v-button class="modal__button" button-type="hollow" @click.native="closeModal">{{` ${modalAppearance[modalType]}`}}</v-button>
        <v-button class="modal__button" button-type="filled" @click.native="saveRequest">{{`${modalAppearance[modalType]}`}}</v-button>
      </div>
    </div>
    <div class="modal__shadow"></div>
  </div>
</template>

<script>
export default {
  name: 'vModal',
  data() {
    return {
      placeholder: ' ',
      modalType: '',
      modalAppearance: {
        save: '',
        edit: '',
      },
      request: {
        name: '',
        body: {
          text: '',
          maxResults: 12,
          order: '',
        },
      },
    };
  },
  methods: {
    closeModal() {
      this.modalType = '';
    },
    receiveSelect(value) {
      this.request.body.order = value;
    },
    saveRequest() {
      if (this.request.name !== '') {
        const favorites = this.getFavorites();
        favorites[this.request.name] = this.request.body;
        this.setFavorites(favorites);
        this.closeModal();
      }
    },
    receiveRange(value) {
      this.request.body.maxResults = value;
    },
  },
  mounted() {
    this.$bus.$on('showModal', ({ type, request }) => {
      this.modalType = type;
      this.request = request;
    });
  },
};
</script>

<style lang="scss" src="styles/components/vModal/vModal.scss"/>
