<template>
  <div class="range">
    <input :max="max" :min="min" class="range__input" type="range" v-model="current">
    <div :style="`left: ${rangeWidth}%`" class="range__thumb"></div>
    <div :style="`width: ${rangeWidth}%`" class="range__filled"></div>
    <div :style="`width: calc(100% - ${rangeWidth}%)`" class="range__empty"></div>
  </div>
</template>

<script>
export default {
  name: 'vRange',
  props: {
    min: { type: Number, default: 0 },
    max: { type: Number, default: 100 },
    default: { type: Number, default: 0 },
  },
  data() {
    return {
      current: this.default,
    };
  },
  methods: {
    emitValue(value) {
      this.$emit('value', value);
    },
  },
  computed: {
    rangeWidth() {
      if (this.current === 0) {
        return 0;
      }
      return (this.current / this.max) * 100;
    },
  },
  watch: {
    current(newValue) {
      this.emitValue(newValue);
    },
  },
};
</script>

<style lang="scss" src="styles/components/_globalComponents/vRange.scss"/>
