<template>
  <div class="search-results" :class="`search-results_${currentView ? 'list' : 'grid'}`">
    <a v-for="(item, index) in videos"
       :key="item.id"
       :href="`https://www.youtube.com/watch?v=${ids[index]}`"
       target="_blank"
       class="search-result"
    >
      <div class="search-result__thumbnail"
           :style="`background-image: url('${item.thumbnails.medium.url}')`"
      ></div>
      <div class="search-result__text">
        <div class="search-result__title" :ref="index">
          <div class="search-result__title-inner" v-html="item.title"></div>
        </div>
        <div class="search-result__info">
          <div class="search-result__description">{{item.description}}</div>
          <div class="search-result__views" v-if="item.statistics !== undefined">
            {{viewCount(item.statistics.viewCount)}}
          </div>
        </div>
      </div>
    </a>
  </div>
</template>

<script>
export default {
  name: 'SearchResults',
  props: {
    videos: {
      type: Array,
      required: true,
    },
    ids: {
      type: Array,
      required: true,
    },
    currentView: {
      type: Boolean,
      required: true,
    },
  },
  data() {
    return {};
  },
  mounted() {
  },
  methods: {
    viewCount(viewCount) {
      let temp;
      if (viewCount >= 1000000000) {
        temp = `${Math.floor(viewCount / 100000000) / 10} . `;
      } else if (viewCount >= 1000000) {
        temp = `${Math.floor(viewCount / 100000) / 10} . `;
      } else if (viewCount >= 10000) {
        temp = `${Math.floor(viewCount / 1000)} . `;
      } else if (viewCount < 10000) {
        temp = `${viewCount} `;
      }
      return temp;
    },
  },
};
</script>

<style lang="scss" src="styles/components/vSearchResults/vSearchResults.scss"/>
