<template>
  <div class="search" :class="response.length > 0 ? 'search_not-empty' : 'search_empty'">
    <div class="search__header">
       
    </div>
    <div class="search__field">
      <input
        class="search__field-input"
        placeholder="  ?"
        v-model="textModel"
        @keydown.enter="runSearch()"
      >
      <i class="search__field-icon icon-heart" @click="addToFavorite(textModel)" v-if="requestText"></i>
      <v-button class="search__field-button" button-type="filled" @click.native="runSearch()"></v-button>
    </div>
    <div class="search__panel" v-if="response.length > 0">
      <div class="search__panel-current-request">
          &nbsp;&nbsp;{{requestText}}
      </div>
      <div class="search__panel-response-counter">
        {{1000000 > totalResults ? totalResults : '1000000+'}}
      </div>
      <div class="search__panel-view-switcher">
        <i class="search__panel-icon icon-list" :class="{'search__panel-icon_active':  currentView}" @click="changeView"/>
        <i class="search__panel-icon icon-grid" :class="{'search__panel-icon_active': !currentView}" @click="changeView"/>
      </div>
    </div>
    <v-search-results
      :currentView="currentView"
      :videos="response"
      :ids="ids"
      v-if="response.length > 0"
    />
  </div>
</template>

<script>
import vSearchResults from './vSearchResults.vue';

export default {
  name: 'vSearch',
  components: {
    vSearchResults,
  },
  data() {
    return {
      textModel: '',
      requestText: '',
      request: {
        searchUrl: 'https://www.googleapis.com/youtube/v3/search',
        videosUrl: 'https://www.googleapis.com/youtube/v3/videos',
        searchParams: {
          part: 'snippet',
          key: 'AIzaSyACDbnvwjKF0A-xWxDHKShZeUjgDxAM_rg',
          maxResults: 12,
          type: 'video',
          q: '',
        },
        viewCountParams: {
          part: 'statistics',
          key: 'AIzaSyACDbnvwjKF0A-xWxDHKShZeUjgDxAM_rg',
        },
      },
      response: [],
      ids: [],
      totalResults: 0,
      currentView: false,
    };
  },
  mounted() {
    if (typeof this.urlParams.get('search') === 'string') {
      this.requestText = this.urlParams.get('search');
      this.runSearch(true);
    }
  },
  methods: {
    changeView() {
      this.currentView = !this.currentView;
    },
    runSearch(fromFavorite = false) {
      if (fromFavorite) {
        this.textModel = this.requestText;
      } else {
        this.requestText = this.textModel;
      }
      if (this.requestText !== '') {
        this.request.searchParams.q = this.requestText;
        this.response = [];
        this.ids = [];
        this.urlVariablesValidation();
        this.axios.get(this.request.searchUrl, { params: this.request.searchParams })
          .then((response) => {
            this.totalResults = response.data.pageInfo.totalResults;
            for (const index in response.data.items) {
              // eslint-disable-next-line no-prototype-builtins
              if (response.data.items.hasOwnProperty(index)) {
                this.response.push(response.data.items[index].snippet);
                this.ids.push(response.data.items[index].id.videoId);
              }
            }
          })
          .then(() => {
            this.getViewsCount();
          })
          .catch((error) => {
            console.log('error', error);
          });
      }
    },
    getViewsCount() {
      this.axios.get(this.request.videosUrl, { params: { ...this.request.viewCountParams, id: this.ids.join(',') } })
        .then((response) => {
          for (const index in response.data.items) {
            // eslint-disable-next-line no-prototype-builtins
            if (response.data.items.hasOwnProperty(index)) {
              this.$set(this.response[index], 'statistics', response.data.items[index].statistics);
            }
          }
        });
    },
    urlVariablesValidation() {
      if (this.requestText.length > 0) {
        this.urlParams.set('search', this.requestText);
      } else {
        this.urlParams.delete('search');
      }
      this.setUrlVariables(this.urlParams);

      if (this.matchStrArr(this.urlParams.get('order'), this.orderTemplate, 'keys')) {
        this.request.searchParams.order = this.urlParams.get('order');
      }

      const maxResults = parseInt(this.urlParams.get('maxResults'), 10);
      if (maxResults > 0 && maxResults <= 50) {
        this.request.searchParams.maxResults = this.urlParams.get('maxResults');
      }
    },
    addToFavorite(text) {
      if (text !== '') {
        this.$bus.$emit('showModal', {
          type: 'save',
          request: {
            name: '',
            body: {
              text,
              maxResults: 12,
              order: '',
            },
          },
        });
      }
    },
  },
};
</script>

<style lang="scss" src="styles/components/vSearch/vSearch.scss"/>
