<template>
  <div class="application">
    <v-header/>
    <div class="body">
      <router-view/>
    </div>
    <v-modal/>
  </div>
</template>

<script>
import vHeader from './vHeader.vue';
import vModal from './vModal.vue';

export default {
  name: 'vUser',
  components: {
    vHeader,
    vModal,
  },
  mounted() {
    this.tokenCheck();
  },
  methods: {
    tokenCheck() {
      const login = this.getLogin();
      if (login) {
        if (this.$route.params.id !== login) {
          this.$route.params.id = login;
        }
      } else {
        this.$router.push('/');
      }
    },
  },
};
</script>

