<template>
  <div class="favorites">
    <div class="favorites__header">
      
    </div>
    <div class="favorites__list" :key="forceUpdate">
      <div class="favorites__item" v-for="(item, index) in favoritesList" :key="item.id">
        <router-link :to="generateHref(item)" class="favorites__item-title">
          {{index}}
        </router-link>
        <div class="favorites__item-panel">
          <div class="favorites__item-button favorites__item-button_change" @click="editFavorite(item, index)"></div>
          <div class="favorites__item-button favorites__item-button_delete" @click="deleteFavorite(index)"></div>
        </div>
      </div>
      <div class="favorites__empty" v-if="Object.keys(favoritesList).length === 0">      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'vFavorites',
  data() {
    return {
      favoritesList: this.getFavorites(),
      forceUpdate: false,
    };
  },
  methods: {
    generateHref(item) {
      let href = `/search/${this.getLogin()}/?search=${item.text}`;

      if (item.maxResults !== 12) {
        href += `&maxResults=${item.maxResults}`;
      }
      if (item.order && item.order !== 'nothing') {
        href += `&order=${item.order}`;
      }
      return href;
    },
    editFavorite(item, index) {
      console.log(item);
      this.$bus.$emit('showModal', { request: { name: index, body: item }, type: 'edit' });
    },
    deleteFavorite(index) {
      const favorites = this.getFavorites();
      delete favorites[index];
      this.setFavorites(favorites);

      delete this.favoritesList[index];
      this.forceUpdate = !this.forceUpdate;
      this.setFavorites(favorites);
    },
  },
};
</script>

<style lang="scss" src="styles/components/vFavorites/vFavorites.scss"/>
