import Vue from 'vue';
import axios from 'axios';
import VueAxios from 'vue-axios';
import VueRouter from 'vue-router';
import Vuex from 'vuex';

import globalFunctions from './assets/js/globalFunctions/_index';
import globalComponents from './components/globalComponents/_index';

import './assets/fonts/index.scss';
import './assets/styles/_globalStyles/_globalStyles.scss';

for (const index in globalComponents) {
  Vue.component(index, globalComponents[index]);
}


// eslint-disable-next-line
import App from './components/App.vue';

// eslint-disable-next-line
import routes from './routes';

Vue.use(VueAxios, axios);
Vue.use(VueRouter);
Vue.use(Vuex);

Vue.mixin({
  data() {
    return {
      urlParams: new URLSearchParams(window.location.search),
      orderTemplate: {
        date: '',
        rating: '',
        relevance: '',
        title: '',
        viewCount: ' ',
      },
    };
  },
  methods: {
    ...globalFunctions,
  },
});

Object.defineProperty(Vue.prototype, '$bus', {
  get() {
    return this.$root.bus;
  },
});

const router = new VueRouter({
  mode: 'history',
  routes,
});


// eslint-disable-next-line no-new
new Vue({
  el: '#app',
  data: {
    bus: new Vue([]),
  },
  router,
  render: (h) => h(App),
});
