import getLogin from './assets/js/globalFunctions/getLogin';

import vUser from './components/vUser.vue';
import vSearch from './components/vSearch.vue';
import vFavorites from './components/vFavorites.vue';
import vAuthorization from './components/vAuthorization.vue';
import vNotFound from './components/vNotFound.vue';

const login = getLogin();

export default [
  {
    path: '/search/:id',
    component: vUser,
    children: [
      { path: '/', component: vSearch },
    ],
  },
  {
    path: '/favorites/:id',
    component: vUser,
    children: [
      { path: '/', component: vFavorites },
    ],
  },
  { path: '/search', redirect: login ? `/search/${login}` : '/' },
  { path: '/favorites', redirect: login ? `/favorites/${login}` : '/' },
  { path: '/', component: vAuthorization },
  { path: '/*', component: vNotFound },
];
