<template>
  <div class="authorization">
    <div class="authorization__logo"></div>
    <div class="authorization__content">
      <div class="authorization__title"></div>
      <div class="authorization__row">
        <div class="authorization__label label"></div>
        <input class="authorization__input input"
               placeholder=" "
               v-model="auth.login"
               @keypress.enter="$refs.pass.focus()"
        >
      </div>
      <div class="authorization__row">
        <div class="authorization__label label"></div>
        <div class="authorization__input-wrapper">
          <input class="authorization__input authorization__input_password input"
                 :style="auth.pass && passwordIsHidden ? 'font-family: icomoon, Roboto, sans-serif; font-size: 11px': ''"
                 :type="passwordIsHidden ? 'password' : 'text'"
                 ref="pass"
                 placeholder=" "
                 v-model="auth.pass"
                 @keypress.enter="login(auth)"
          >
          <i class="authorization__icon"
             :class="passwordIsHidden ? 'icon-eye-slash' : 'icon-eye'"
             @click="togglePassword"
             tabindex="-1"
          ></i>
        </div>
      </div>
      <div class="authorization__error" v-if="error">   </div>
    </div>
    <v-button class="authorization__button"
              button-type="filled"
              @click.native="login(auth)"
    ></v-button>
  </div>
</template>

<script>
export default {
  name: 'vAuthorization',
  data() {
    return {
      auth: {
        login: '',
        pass: '',
      },
      passwordIsHidden: true,
      error: false,
    };
  },
  methods: {
    togglePassword() {
      this.passwordIsHidden = !this.passwordIsHidden;
      this.$refs.pass.focus();
    },
    async login(auth) {
      const token = this.encodeB64(JSON.stringify(auth));
      if (await this.authorization(token)) {
        localStorage.setItem('token', token);
      } else {
        this.error = true;
      }
      if (this.getLogin()) {
        await this.$router.push(`/search/${this.getLogin()}`);
      }
    },
  },
  watch: {
    // eslint-disable-next-line func-names
    'auth.login': function () {
      if (this.error === true) {
        this.error = false;
      }
    },
    // eslint-disable-next-line func-names
    'auth.pass': function () {
      if (this.error === true) {
        this.error = false;
      }
    },
  },
};
</script>

<style lang="scss" src="styles/components/vAuthorization/vAuthorization.scss"/>
