export default function matchStrArr(string, rawArray, keysOrValues = 'values') {
  let array = [];
  if (typeof rawArray === 'object') {
    if (rawArray.isArray) {
      array = rawArray;
    } else if (keysOrValues === 'keys') {
      array = Object.keys(rawArray);
    } else {
      array = Object.values(rawArray);
    }
  } else {
    array.push(rawArray);
  }
  return array.filter((item) => item.indexOf(string) === 0).length > 0;
}
