<template>
  <div class="select input" :class="{'input_active': dropdownActive}" ref="select">
    <div class="select__current-item"
         :class="{'select__current-item_placeholder': currentItem.value === '' || currentItem.value === placeholder}"
         @click="toggleDropdown"
    >{{currentItem.value}}</div>
    <div class="select__dropdown" :class="{'select__dropdown_active': dropdownActive}">
      <div class="select__dropdown-item"
           @click="itemPick(placeholder)"
      >{{placeholder}}</div>
      <div v-for="(item, index) in list" :key="item.id"
           class="select__dropdown-item"
           @click="itemPick(item, index)"
      >
        {{item}}
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'vSelect',
  props: {
    list: {
      type: Object,
      required: true,
    },
    placeholder: {
      type: String,
      default: '',
    },
    defaultItem: {
      type: String,
      default: '',
    },
  },
  data() {
    return {
      currentItem: {
        index: '',
        value: this.placeholder,
      },
      dropdownActive: false,
    };
  },
  mounted() {
    if (this.defaultItem !== '') {
      this.currentItem = { value: this.list[this.defaultItem], index: this.defaultItem };
    }
  },
  methods: {
    emitValue(value) {
      this.$emit('value', value);
    },
    itemPick(item, index) {
      this.currentItem.value = item;
      this.currentItem.index = index;
      this.dropdownActive = false;
    },
    toggleDropdown() {
      this.dropdownActive = !this.dropdownActive;
      if (this.dropdownActive) {
        document.addEventListener('click', this.dropdownAutoClose);
      }
    },
    dropdownAutoClose(event) {
      if (!this.$refs.select.contains(event.target)) {
        this.dropdownActive = false;
      }
    },
  },
  watch: {
    // eslint-disable-next-line func-names
    'currentItem.index': function (newValue) {
      this.emitValue(newValue);
    },
    dropdownActive(newValue) {
      if (!newValue) {
        document.removeEventListener('click', this.dropdownAutoClose);
      }
    },
  },
};
</script>

<style lang="scss" src="styles/components/_globalComponents/vSelect.scss"/>
